}}.swiper-slide .inf_slide p{color:white;font-size:15px}@media only screen and (min-width: 992px){.swiper-slide .inf_slide p{font-size:17px}}@media only screen and (min-width: 1200px){.swiper-slide .inf_slide p{font-size:20px}}.swiper-button-next,.swiper-button-prev{display:none}@media only screen and (min-width: 768px){.swiper-button-next,.swiper-button-prev{display:flex}}.swiper-button-next{right:20px}header{width:100vw;height:100vh;position:relative}header nav{width:100%;margin:auto;display:flex;justify-content:space-between;padding:20px 15px;z-index:1;background-color:white;align-items:center;position:relative;flex-wrap:wrap}@media only screen and (min-width: 768px){header nav{padding:20px 30px}}@media only screen and (min-width: 992px){header nav{margin-top:20px;width:95%;background-color:transparent;padding:7px 0px;justify-content:space-around}}@media only screen and (min-width: 1200px){header nav{width:85%}}header nav label{display:flex;flex:0 1 50px;order:-1}@media only screen and (min-width: 992px){header nav label{display:none}}header nav .logo{flex:1}header nav .logo img{float:right}@media only screen and (min-width: 992px){header nav .logo img{float:left}}header nav ul.navigation{padding:0;display:flex;flex-direction:column;height:0px;justify-content:flex-end;width:100%}@media only screen and (min-width: 992px){header nav ul.navigation{width:auto;position:relative;background-color:rgba(255,255,255,0);flex-direction:row;height:auto;flex:0 1 70%}}header nav ul.navigation li{list-style:none;padding:10px 0px;position:relative}header nav ul.navigation li::before{content:'';position:absolute;top:0;width:95%;height:1px;background-color:rgba(78,78,78,0.6)}@media only screen and (min-width: 992px){header nav ul.navigation li::before{width:0}}header nav ul.navigation li:first-child::before{width:0}@media only screen and (min-width: 1200px){header nav ul.navigation li{padding:0px 12px}}header nav ul.navigation li a{color:black;font-size:17px;font-weight:400;text-de